import { motion } from 'framer-motion';
import Link from 'next/link';

export type Status = 'Completed' | 'In Progress' | 'Not Started';

interface CardProps {
  title: string;
  description: string;
  status: Status;
  githubLink?: string;
  detailsLink?: string;
  tags?: string[];
}

const statusColors = {
  'Completed': 'bg-success text-white',
  'In Progress': 'bg-primary text-white',
  'Not Started': 'bg-gray-600 text-white',
};

export default function Card({
  title,
  description,
  status,
  githubLink,
  detailsLink,
  tags = [],
}: CardProps) {
  return (
    <motion.div
      whileHover={{ y: -5 }}
      className="bg-background-darker rounded-lg p-6 shadow-lg hover:shadow-primary/10 transition-shadow"
    >
      <div className="flex justify-between items-start mb-4">
        <h3 className="text-xl font-semibold text-text-primary">{title}</h3>
        <span className={`px-3 py-1 rounded-full text-sm ${statusColors[status]}`}>
          {status}
        </span>
      </div>
      
      <p className="text-text-secondary mb-4">{description}</p>
      
      {tags.length > 0 && (
        <div className="flex flex-wrap gap-2 mb-4">
          {tags.map((tag) => (
            <span
              key={tag}
              className="px-2 py-1 bg-background-dark rounded-md text-xs text-text-secondary"
            >
              {tag}
            </span>
          ))}
        </div>
      )}
      
      <div className="flex gap-3 mt-auto">
        {githubLink && (
          <Link
            href={githubLink}
            className="text-primary hover:text-primary-hover text-sm transition-colors"
            target="_blank"
            rel="noopener noreferrer"
          >
            GitHub
          </Link>
        )}
        {detailsLink && (
          <Link
            href={detailsLink}
            className="text-secondary hover:text-secondary-hover text-sm transition-colors"
          >
            View Details
          </Link>
        )}
      </div>
    </motion.div>
  );
} 